/**
 * @typedef BoardState –°–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
 * @property {number} x –ö–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ X
 * @property {number} y –ö–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ Y
 * @property {boolean} withBomb –Ø—á–µ–π–∫–∞ —Å –±–æ–º–±–æ–π
 * @property {number} nearBombsCount –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–æ–º–± —Ä—è–¥–æ–º —Å —è—á–µ–π–∫–æ–π
 * @property {boolean} isOpened –Ø—á–µ–π–∫–∞ –æ—Ç–∫—Ä—ã—Ç–∞
 * @property {boolean} isMarked –Ø—á–µ–π–∫–∞ –ø–æ–º–µ—á–µ–Ω–∞ –Ω–∞ –≤–æ–∑–º–æ–∂–Ω–æ–µ –Ω–∞–ª–∏—á–∏–µ –±–æ–º–±—ã
 * @property {boolean} isHelper
 */

/**
 * –õ–æ–≥–∏–∫–∞ –∏–≥—Ä—ã "–°–∞–ø–µ—Ä"
 */
export default class Logic {
  /**
   * –°–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   * @param {number} width –®–∏—Ä–∏–Ω–∞ –ø–æ–ª—è
   * @param {number} height –í—ã—Å–æ—Ç–∞ –ø–æ–ª—è
   * @param {number} bombsCount –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–æ–º–± –Ω–∞ –ø–æ–ª–µ
   * @returns {BoardState[][]} –°–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   */
  static initBoard(width, height, bombsCount) {
    /* –†–∞—Å—Å—Ç–∞–≤–ª—è–µ–º –±–æ–º–±—ã. */

    const bombs = [];
    const isBomb = (x, y) => bombs.filter(bomb => bomb.x === x && bomb.y === y).length;

    for (let i = 0; i < bombsCount; i++) {
      const x = Math.floor(Math.random() * width);
      const y = Math.floor(Math.random() * height);

      if (isBomb(x, y))
        i--;
      else
        bombs.push({ x, y });
    }

    /* –ü–æ–º–µ—á–∞–µ–º –¥–ª—è –∫–∞–∂–¥–æ–π —è—á–µ–π–∫–∏ —Å–∫–æ–ª—å–∫–æ —Ä—è–¥–æ–º —Å –Ω–µ–π –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –±–æ–º–±. */

    const nearBombs = [];

    for (let y = 0; y < height; y++) {
      for (let x = 0; x < width; x++) {
        if (isBomb(x, y)) continue;

        let count = 0;

        for (let dy = -1; dy <= 1; dy++)
          for (let dx = -1; dx <= 1; dx++)
            if (isBomb(x - dx, y - dy))
              count++;

        if (count)
          nearBombs.push({ x, y, count });
      }
    }

    /* –°–æ–∑–¥–∞–µ–º –ø–æ–ª–µ. */

    return Array(height).fill()
      .map((_, y) => Array(width).fill()
        .map((_, x) => {
          const filtered = nearBombs.filter(cell => cell.x === x && cell.y === y);

          return {
            x, y,
            withBomb: Boolean(isBomb(x, y)),
            nearBombsCount: filtered.length ? filtered[0].count : 0,
            isOpened: false,
            isMarked: false,
            isHelper: false,
          };
        })
      );
  }

  /**
   * –û—Ç–∫—Ä—ã—Ç—å —è—á–µ–π–∫—É –∏, –ø–æ –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç–∏, –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ —Ä—è–¥–æ–º —Å –Ω–µ–π, –µ—Å–ª–∏ –æ–Ω–∏ –ø—É—Å—Ç—ã–µ.
   * @param {BoardState[][]} boardState –ü–æ—Å–ª–µ–¥–Ω–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   * @param {BoardState} currSquare –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {BoardState[][]} –ù–æ–≤–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   */
  static openSquare(boardState, currSquare) {
    const newBoardState = boardState.map(row => row.map(sqr => sqr));
    const newCurrSquare = newBoardState[currSquare.y][currSquare.x];

    openSquareRec(newBoardState, newCurrSquare);

    if (newCurrSquare.isOpened && newCurrSquare.withBomb) {
      newBoardState.forEach(row => row.forEach(square => square.isOpened = true));
      alert('–¢—ã –ø—Ä–æ–∏–≥—Ä–∞–ª.');
    }

    return newBoardState;
  }

  /**
   * –ü–æ—Å—Ç–∞–≤–∏—Ç—å –∏–ª–∏ —Å–Ω—è—Ç—å –º–µ—Ç–∫—É –Ω–∞ –∑–∞–∫—Ä—ã—Ç—É—é —è—á–µ–π–∫—É ("!" –∏–ª–∏ "?").
   * @param {BoardState[][]} boardState –ü–æ—Å–ª–µ–¥–Ω–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   * @param {BoardState} currSquare –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {BoardState[][]} –ù–æ–≤–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   */
  static setMark(boardState, currSquare) {
    if (currSquare.isOpened)
      return boardState;

    return boardState.map(row => (
      row.map(square => {
        if (!(square.x === currSquare.x && square.y === currSquare.y))
          return square;

        if (!square.isMarked && !square.isHelper)
          square = { ...square, isMarked: true };
        else if (square.isMarked && !square.isHelper)
          square = { ...square, isMarked: false, isHelper: true };
        else if (!square.isMarked && square.isHelper)
          square = { ...square, isHelper: false };

        return square;
      })
    ))
  }

  /**
   * –û—Ç–∫—Ä—ã—Ç—å —Å–æ—Å–µ–¥–Ω–∏–µ —è—á–µ–π–∫–∏, –µ—Å–ª–∏ —Å—Ä–µ–¥–∏ –Ω–∏—Ö –Ω–µ—Ç –±–æ–º–±.
   * @param {BoardState[][]} boardState –ü–æ—Å–ª–µ–¥–Ω–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   * @param {BoardState} currSquare –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {BoardState[][]} –ù–æ–≤–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   */
  static tryOpenNearSquares(boardState, currSquare) {
    let isBombsNear = false; 

    doWithNearSquares(boardState, currSquare, square => {
      if (isBombsNear) return;

      if (square.isOpened || square.isMarked) return;

      if (square.withBomb)
        isBombsNear = true;
    });

    if (isBombsNear)
      return boardState;

    const newBoardState = boardState.map(row => row.map(sqr => sqr));

    doWithNearSquares(newBoardState, currSquare, square => {
      if (square.isOpened || square.isMarked) return;

      square.isOpened = true;
    });

    return newBoardState;
  }

  /**
   * –ü–æ–∫–∞–∑–∞—Ç—å –≤ –∫–æ–Ω—Å–æ–ª–∏ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –±–æ–º–± –Ω–∞ –ø–æ–ª–µ.
   * @param {BoardState[][]} boardState –°–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
   */
  static logBoard(boardState) {
    console.log(
      boardState.map(row => (
        row.map(square => (
          square.withBomb ? 'Q' : square.nearBombsCount ? square.nearBombsCount + '' : ' '
        ))
      ))
    );
  }
}

/**
 * –° –ø–æ–º–æ—â—å—é —Ä–µ–∫—É—Ä—Å–∏–∏ –æ—Ç–∫—Ä—ã—Ç—å –≤—Å–µ –ø—É—Å—Ç—ã–µ —è—á–µ–π–∫–∏ –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–π –¥–æ —è—á–µ–µ–∫ —Å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ–º —Ä—è–¥–æ–º –Ω–∞—Ö–æ–¥—è—â–∏—Ö—Å—è –±–æ–º–±.
 * @param {BoardState[][]} boardState –°–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
 * @param {BoardState} currSquare –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 */
function openSquareRec(boardState, currSquare) {
  if (currSquare.isOpened || currSquare.isMarked)
    return;

  currSquare.isOpened = true;

  if (currSquare.withBomb || currSquare.nearBombsCount)
    return;

  doWithNearSquares(boardState, currSquare, square => openSquareRec(boardState, square));
};

/**
 * @callback nearSquareCallback
 * @param {BoardState} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 */

/**
 * –°–¥–µ–ª–∞—Ç—å —á—Ç–æ-—Ç–æ —Å –±–ª–∏–∂–∞–π—à–∏–º–∏ –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–π —è—á–µ–π–∫–∞–º–∏ 
 * @param {BoardState[][]} boardState –°–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—è
 * @param {BoardState} currSquare –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 * @param {nearSquareCallback} callback –ö–æ–ª–±—ç–∫
 */
function doWithNearSquares(boardState, currSquare, callback) {
  const x = currSquare.x;
  const y = currSquare.y;

  for (let dy = -1; dy <= 1; dy++)
    for (let dx = -1; dx <= 1; dx++) {
      if (!(boardState[y + dy] && boardState[y + dy][x + dx])) continue;

      const square = boardState[y + dy][x + dx];

      if (!square) continue;
      if (square.x === x && square.y === y) continue;

      callback(square);
    }
}
